const p0 = (n: number) => n < 10 ? '0' + n : '' + n
export const getTime = (n: number, showHour: boolean = false): [string, number] => {
  n = ~~(n / 1000)
  let second = n % 60;
  n -= second;
  n = ~~(n / 60) // 
  let minute = (n % 60)// 
  let hour = ~~(n / 60)

  if (hour === 0 && showHour === false) {
    return [`${p0(minute)}:${p0(second)}`, hour]
  }
  return [`${hour}:${p0(minute)}:${p0(second)}`, hour]
}
